.layout {
  display: flex;
  width: 100%;
  height: 100%;
  padding: 12px;
  background-color: #000;
  .mainContainer {
    width: 100%;
    height: 100%;
    flex: 1;
    border-radius: 12px;
    margin-right: 12px;
    overflow: hidden;
    position: relative;
    &::before {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      box-shadow: inset 0px 1px 8px 5px rgba(0, 0, 0, 0.3);
      border-radius: 12px;
      pointer-events: auto;
      z-index: 1;
    }
  }
  .miniContainer {
    max-width: 320px;
    width: 24%;
    flex-shrink: 0;
    height: 100%;
  }
  .backdropContainer {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    // filter: blur(15px);
  }
}
